.hero-img{
  background-image: url('../../../assets/hero.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 15% center;
  width: 100%;
  min-height: 30vh;
}
.hero .about-me{
  color: black;
  background-color: rgba(255,255,255,0.3);
  padding: 1em 1.5em;
  text-align: center;
  display: flex;
  gap: 10px;
  font-size: 20px;
  flex-direction: column;
}