.nav{
  display: flex;
  justify-content: space-between;
  padding: 2em;
  font-size: 24px;
  align-items: center;
  background-color: white;
  flex-wrap: wrap;
}

@media screen and (width<=550px) {
  .nav{
    justify-content: center;
    gap: 10px;
  };
};

.nav h3{
  text-wrap: nowrap;
}
.nav ol{
  list-style-type: none;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
}
.nav ol li button{
  font-size: 22px;
  border: 0px solid transparent;
  background-color: transparent;
  display: flex;
}