.footer{
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 2em;
}
.footer ol{
  list-style-type: none;
  display: flex;
  justify-content: center;
  gap: 10px;
}
.footer ol li button{
  border: 0px solid transparent;
  background-color: transparent;
}
.footer ol li button img{
  min-width: 35px;
  cursor: pointer;
  color: #c5c4c4;
}